export default{
    starter_data: {
        //Generics used in all boards
        variables: {},   
        breakpoints: [],
        external_sensor_names: [], 
        external_sensor_values: {},
        //Starter values for the micro:bit's digital, analog, and pwm pins 
        digital : { "D2" : [0], "D3" : [0], "D4": [0], "D6" : [0], "D9" : [0], "D10" : [0], "D12" : [0], "D19": [0], "D21": [0]}, 
        analog : {"A7" : [0], "A9" : [0], "A10" : [0], "A11" : [0]},  
        pwm : {"D3" : 0, "D6" : 0, "D9" : 0, "D10" : 0}, 
        motion: [], 
        temp:[], 
        light:[],
        microphone: []
    },
    pins://Maps each physical pin location to it's 
         //  internal digital, analog, and/or pwm designation.
         //If analog or pwm are left out, then it is assumed that pin location does
         //  not support analog or pwm signals
        [
            //{name: "#0", digital: "D0"},//RX
            //{name: "#1", digital: "D1"},//TX
            {name: "#2", digital: "D2", output:true, input:true},
            {name: "#3", digital: "D3", pwm: "D3", output:true, input:true},  
            {name: "#6", digital: "D6", analog: "A7", pwm: "D6", output:true, input:true},
            {name: "#9", digital: "D9", analog: "A9", pwm: "D9", output:true, input:true},
            {name: "#10", digital: "D10", analog: "A10", pwm: "D10", output:true, input:true}, 
            {name: "#12", digital: "D12", analog: "A11", output:true, input:true},
            {name: "#4", digital: "D4", output:false, input:true},
            {name: "#19", digital: "D19", output:false, input:true},
            {name: "#21", digital: "D21", output:false, input:true},
            {name: "#13", digital: "D13", output:true, input:false}
        ],
    onboard_sensors://Details all of the onboard_sensors
        [
            "Temp", 
            "Light", 
            "Motion", 
            "Microphone", 
            "Speaker", 
            "Neopixel", 
        ],
    modes: //Describes the potential digital output modes for the microcontroller
    [
        "INPUT", 
        "OUTPUT",
        "INPUT PULLUP"
    ],
    modeText: "Mode",//Term used for describing digital output modes (default for Arduino is pinMode)
    basicHeight: "475",
    advanceHeight: "280",
    advanceY: "40"
};